@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700,700i');
/*
-- Layout styles --
*/
html {
  font-size: 16px;
}
body {
  background: linear-gradient(to bottom, #FFF 0%, #F6F6F6 47%, #EDEDED 100%);
  font-family: 'Open Sans', sans-serif;
}
.layout {
  min-height: 100vh;
  max-width: 780px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.datepicker-custom-wraper > [class*="datepicker"] {
  display: block;
}
/*
-- Header --
*/
.header-app .user-name {
  font-weight: bold;
  text-transform: capitalize;
}
/*
-- Login Panel --
*/
.login-panel {
  min-width: 280px;
}
.login-panel button {
  min-width: 80px;
}
/*
-- Register Panel --
*/
.register-panel {
  min-width: 360px;
}
.register-success {
  position: relative;
  font-size: 16px;
  padding-left: 36px;
}
.register-success .glyphicon {
  position: absolute;
  left: 10px;
  top: 15px;
  font-size: 18px;
}
/*
-- Main table --
*/
.main-table-wrapper {
  text-align: center;
}
.main-table-info {
  font-size: 16px;
}
.main-table {
  min-width: 560px;
  background-color: #FFF;
  border-radius: 4px;
  border-style: hidden;
  box-shadow: 0 0 0 1px #DDD;
  text-align: left;
}
.main-table tr td:last-child {
  text-align: center;
}
.main-table .remove-entry {
  font-size: 10px;
  vertical-align: middle;
}
.main-table .remove-entry:hover {
  cursor: pointer;
}
.main-table .col-name {
  width: 32%;
}
.main-table .col-email {
  width: 38%;
}
.main-table .col-date {
  width: 22%;
}
.main-table .col-remove {
  width: 8%;
}
/*
-- Entry creator --
*/
.entry-creator {
  min-width: 360px;
}
/*
-- Entry editor --
*/
.entry-editor {
  min-width: 360px;
}
/*
-- Footer --
*/
.footer-app {
  line-height: 24px;
}



